.supercluster div {
  box-sizing: border-box; }

.supercluster .point-icon {
  height: 100%;
  width: 100%;
  display: block;
  border-radius: 50%;
  background: coral;
  border: 2px solid #FFF;
  font-weight: 700;
  position: relative; }
  .supercluster .point-icon .pulsate {
    display: none; }

.supercluster .cluster-icon {
  height: 100%;
  width: 100%;
  display: block;
  border-radius: 50%;
  background: coral;
  text-align: center;
  padding-top: 12px;
  color: #fff;
  font-weight: 700;
  border: 2px solid #fff; }

.supercluster.opened {
  opacity: 0.3; }

.supercluster.animate-add .point-icon {
  animation: supercluster-show 300ms; }
  .supercluster.animate-add .point-icon .pulsate {
    width: 25px;
    height: 25px;
    top: -7.5px;
    left: -7.5px;
    position: absolute;
    display: block;
    background: rgba(255, 127, 80, 0.6);
    border-radius: 50%;
    opacity: 0;
    animation: supercluster-pulsate 2400ms ease-out infinite; }

.supercluster.animate-add .cluster-icon {
  animation: supercluster-show 300ms; }

.supercluster.animate-move {
  transition: transform 300ms linear; }

.leaflet-zoom-anim .supercluster.animate-move {
  transition: none; }

@keyframes supercluster-show {
  0% {
    transform: scale(0);
    opacity: 0; }
  100% {
    transform: scale(1);
    opacity: 1; } }

@keyframes supercluster-pulsate {
  0% {
    transform: scale(0.1);
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    transform: scale(1.2);
    opacity: 0; } }
